import React, { useState, useEffect } from 'react'
import { useSelector } from 'react-redux'
import styled from 'styled-components'
import { getPassengers } from '../../redux/schedule/scheduleSelectors'
import { Button } from '../../stylesheets/mainStyled'

const PassengersFilter = ({
  vehicles,
  selected,
  onFilter,
  onSelected,
  onClearFilter,
}) => {
  const passengers = useSelector(getPassengers)
  const [localitiesFrom, setLocalitiesFrom] = useState([])
  const [stopsFrom, setStopsFrom] = useState([])
  const [localitiesTo, setLocalitiesTo] = useState([])

  useEffect(() => {
    setLocalitiesFrom([
      ...new Set(passengers.orders.map(el => passengers.localities[el.fromId].parent.id)),
    ])
    setLocalitiesTo([
      ...new Set(passengers.orders.map(el => passengers.localities[el.toId].parent.id)),
    ])
    setStopsFrom([...new Set(passengers.orders.map(el => el.fromId))])
  }, [])

  const handleChange = ({ target }) => onSelected(target)
  const getFilter = () => onFilter(selected)
  return (
    <Container>
      <ClearButton type="button" onClick={onClearFilter}>
        Сбросить фильтр
      </ClearButton>
      <Filters>
        <div>
          <Title>Автобус</Title>
          <List>
            {vehicles.map(bus => (
              <ListItem key={bus.tripVehicleId}>
                <label>{bus.number}</label>
                <input
                  type="checkbox"
                  name="bus"
                  value={bus.tripVehicleId}
                  checked={selected.bus.some(el => el === bus.tripVehicleId)}
                  onChange={handleChange}
                />
              </ListItem>
            ))}
          </List>
        </div>
        <div>
          <Title>Город отправления</Title>
          <List>
            {localitiesFrom.map(id => (
              <ListItem key={id}>
                <label>{passengers.localities[id].name['RU']}</label>
                <input
                  type="checkbox"
                  name="localityFrom"
                  value={id}
                  checked={selected.localityFrom.some(el => el === id)}
                  onChange={handleChange}
                />
              </ListItem>
            ))}
          </List>
        </div>
        <div>
          <Title>Остановка отправления</Title>
          <List>
            {stopsFrom.map(id => (
              <ListItem key={id}>
                <label>{passengers.localities[id].name['RU']}</label>
                <input
                  type="checkbox"
                  name="stopFrom"
                  value={id}
                  checked={selected.stopFrom.some(el => el === id)}
                  onChange={handleChange}
                />
              </ListItem>
            ))}
          </List>
        </div>
        <div>
          <Title>Город прибытия</Title>
          <List>
            {localitiesTo.map(id => (
              <ListItem key={id}>
                <label>{passengers.localities[id].name['RU']} </label>
                <input
                  type="checkbox"
                  name="localityTo"
                  value={id}
                  checked={selected.localityTo.some(el => el === id)}
                  onChange={handleChange}
                />
              </ListItem>
            ))}
          </List>
        </div>
      </Filters>
      <Button type="button" onClick={getFilter}>
        Отфильтровать
      </Button>
    </Container>
  )
}

export default PassengersFilter

const Container = styled.div`
  background-color: #fff;
  padding: 20px;
  border-radius: 6px;
  min-width: 300px;
`
const ClearButton = styled.button`
  display: block;
  margin: 0 0 10px auto;
  background: none;
  border: none;
  text-decoration: underline;
`
const Filters = styled.div`
  display: flex;
  flex-direction: column;
`
const Title = styled.h4`
  margin-bottom: 10px;
`
const List = styled.ul`
  display: flex;
  flex-direction: column;
`

const ListItem = styled.li`
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  & input {
    width: 16px;
    height: 16px;
  }
`
