import React, { useEffect, useCallback, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link } from 'react-router-dom'
import moment from 'moment'
import styled from 'styled-components'
import { ReactComponent as ArrowBackIcon } from '../images/arrowBack.svg'
import { ReactComponent as FilterIcon } from '../images/filter.svg'
import { getPassengersAll } from '../redux/schedule/scheduleOperations'
import { getPassengers, getSchedule } from '../redux/schedule/scheduleSelectors'
import { useQuery } from '../services/useCustomQuery'
import Order from '../components/ScheduleContainer/Order'
import Modal from '../components/Modal'
import PassengersFilter from '../components/ScheduleContainer/PassengersFilter'

const SELECTED = {
  bus: [],
  localityFrom: [],
  stopFrom: [],
  localityTo: [],
}
const PassengersPage = () => {
  const dispatch = useDispatch()
  const query = useQuery()
  const departure = moment(query.get('departure')).format('YYYY-MM-DD')
  const tripId = query.get('tripId')

  const passengers = useSelector(getPassengers)
  const trip = useSelector(getSchedule).find(
    trip =>
      trip.id === tripId &&
      trip.departure === moment(query.get('departure')).format('YYYY-MM-DD HH:mm')
  )

  const [isFilters, setIsFilters] = useState(false)
  const [filtredPassengers, setFiltredPassengers] = useState([])
  const [selected, setSelected] = useState(SELECTED)

  const vehicles = trip.hasOwnProperty('vehicles') ? trip.vehicles : []

  const passengersInfo = useCallback(
    (departure, tripId) => dispatch(getPassengersAll(departure, tripId)),
    [dispatch]
  )
  useEffect(() => {
    passengersInfo(departure, tripId)
  }, [departure, passengersInfo, tripId])

  useEffect(() => {
    if (passengers) setFiltredPassengers(passengers.orders)
  }, [passengers])

  const toggleModal = () => {
    setIsFilters(!isFilters)
  }

  const onSelected = target => {
    const name = target.name
    const value = target.value
    if (selected[name].some(el => el === value))
      setSelected(prev => ({ ...prev, [name]: prev[name].filter(el => el !== value) }))
    if (!selected[name].some(el => el === value))
      setSelected(prev => ({ ...prev, [name]: [...prev[name], value] }))
  }

  const onFilter = () => {
    if (
      selected.bus.length === 0 &&
      selected.localityFrom.length === 0 &&
      selected.stopFrom.length === 0 &&
      selected.localityTo.length === 0
    ) {
      setFiltredPassengers(passengers.orders)
      toggleModal()
      return
    }

    let result = []
    selected.bus.length > 0
      ? (result = [
          ...passengers.orders.filter(order =>
            selected.bus.includes(order.tripVehicleId)
          ),
        ])
      : (result = [...passengers.orders])

    selected.localityFrom.length > 0
      ? (result = [
          ...result.filter(order =>
            selected.localityFrom.includes(passengers.localities[order.fromId].parent.id)
          ),
        ])
      : (result = [...result])

    selected.stopFrom.length > 0
      ? (result = [...result.filter(order => selected.stopFrom.includes(order.fromId))])
      : (result = [...result])

    selected.localityTo.length > 0
      ? (result = [
          ...result.filter(order =>
            selected.localityTo.includes(passengers.localities[order.toId].parent.id)
          ),
        ])
      : (result = [...result])

    setFiltredPassengers(result)
    toggleModal()
  }

  const onClearFilter = () => {
    setFiltredPassengers(passengers.orders)
    toggleModal()
    setSelected(SELECTED)
  }
  return (
    <Container>
      <Header>
        <Link to="/schedule">
          <ArrowBackIcon />
        </Link>
        <Title>
          {moment(query.get('departure')).format('HH:mm')} {trip.name}
        </Title>
        <FilterIcon onClick={toggleModal} />
      </Header>
      <ListOrders>
        {filtredPassengers.length > 0 &&
          filtredPassengers.map(order => <Order key={order.id} order={order} />)}
      </ListOrders>
      <Modal open={isFilters} onClose={toggleModal}>
        <PassengersFilter
          vehicles={vehicles}
          selected={selected}
          onSelected={onSelected}
          onFilter={onFilter}
          onClearFilter={onClearFilter}
        />
      </Modal>
    </Container>
  )
}

export default PassengersPage

const Container = styled.div``
const Header = styled.header`
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: ${prop => prop.theme.mainColor};
  padding: 12.4px 24px;
`
const ListOrders = styled.div`
  padding: 20px;
`
const Title = styled.p`
  font-size: 24px;
`
