import React from 'react'
import { useSelector } from 'react-redux'
import styled from 'styled-components'
import { device } from '../../stylesheets/device'
import { ReactComponent as ArrowDownIcon } from '../../images/arrowDropdown.svg'
import { ReactComponent as PhoneIcon } from '../../images/phone.svg'
import { getPassengers, getSchedule } from '../../redux/schedule/scheduleSelectors'
import moment from 'moment'
import { useQuery } from '../../services/useCustomQuery'

const Order = ({ order }) => {
  const query = useQuery()

  const localities = useSelector(getPassengers).localities
  const tripId = query.get('tripId')
  const trip = useSelector(getSchedule).find(
    trip =>
      trip.id === tripId &&
      trip.departure === moment(query.get('departure')).format('YYYY-MM-DD hh:mm')
  )
  const vehicles = trip.hasOwnProperty('vehicles') ? trip.vehicles : []

  const getLocality = id => {
    const parent = localities[id].parent.id
    return localities[parent].name['RU']
  }
  const getAddress = id => {
    const address = localities[id].name
    return address['RU'] || address['UA'] || address['EN'] || address['PL']
  }
  const getVehicle = () => {
    const vehicleNumber = vehicles.find(
      el => el.tripVehicleId === order.tripVehicleId
    ).number
    return vehicleNumber
  }
  return (
    <Box>
      <div>
        <Text>
          Заказ {order.id} {getLocality(order.fromId)} - {getLocality(order.toId)}
        </Text>
        <Text>
          {order.departureTime} {getAddress(order.fromId)}
        </Text>
        <Bus>
          <BusNumber>{getVehicle()}</BusNumber> <ArrowDownIcon />
        </Bus>
      </div>

      <List>
        {order.customers.map(passenger => (
          <ListItem key={passenger.id}>
            <PhoneLink href={`tel: +${passenger.phone}`}>
              <PhoneIcon />
              <div>
                <Text bold>
                  {passenger.surname} {passenger.name}
                </Text>
                <Text>{passenger.phone}</Text>
              </div>
            </PhoneLink>
            <Checkbox type="checkbox" />
          </ListItem>
        ))}
      </List>
    </Box>
  )
}
export default Order

const Box = styled.div`
  border-bottom: ${prop => prop.theme.border};
  margin-bottom: 24px;
  padding: 20px;
  &:last-child {
    margin-bottom: 0;
  }
  @media ${device.tablet} {
    display: flex;
    justify-content: flex-start;
    & > div {
      margin-right: 20px;
    }
  }
`
const Text = styled.p`
  font-size: 18px;
  margin-bottom: 6px;
  font-weight: ${prop => (prop.bold ? 700 : 400)};
  color: ${prop => prop.theme.textColorDark};
`
const Bus = styled.div`
  margin-bottom: 6px;
  padding: 6px 12px;
  width: 140px;
  border: ${prop => prop.theme.border};
  border-radius: 4px;
  background: none;
  display: flex;
  align-items: center;
  justify-content: center;
`
const BusNumber = styled.p`
  font-size: 18px;
  margin-right: 12px;
`
const List = styled.ul`
  padding: 0;
  margin: 0;
  max-width: 360px;
  @media ${device.tablet} {
    flex: 1;
  }
`
const ListItem = styled.li`
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
`
const PhoneLink = styled.a`
  display: flex;
  align-items: center;
  & div {
    padding-left: 12px;
  }
  & p:last-child {
    margin: 0;
  }
`

const Checkbox = styled.input`
  width: 18px;
  height: 18px;
  border-width: 2px;
`
